import axios from 'axios'
import {msgBoxNoLogin, msgBoxNoRole, msgError} from './dw-msg'

//  axios 
axios.defaults.baseURL = process.env.DW_API_URL
// 
axios.defaults.timeout = 10000
// post
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'

const service = axios.create({
  // axiosbaseURLURL
  baseURL: process.env.DW_API_URL,
  // 
  timeout: 10000
})

// 
service.interceptors.request.use(
  config => {
    return config
  },
  error => {
    console.log('request-error:'+error)
    return Promise.error(error)
  })

// 
service.interceptors.response.use(
  response => {
    console.debug('response')
    console.debug(response)
    if (response.status === 200) {
      const {data} = response
      if (data.hasOwnProperty('resultCode')) {
        if (data.resultCode === 401) {
          msgBoxNoLogin()
        } else if (data.resultCode === 403) {
          msgBoxNoRole()
        } else if (data.resultCode !== 200) {
          let message = `error code${data.resultCode}`
          if (data.hasOwnProperty('resultMsg')) {
            message = `${data.resultMsg}error code${data.resultCode}`
          }
          msgError(message)
        }
      }
      return Promise.resolve(response)
    } else {
      return Promise.reject(response)
    }
  },
  error => {
    // 200
    console.log('err' + error)
    let {message} = error
    if (message.includes('Network Error')) {
      message = 'back end timeout has error'
    } else if (message.includes('timeout')) {
      message = 'system API timeout'
    } else if (message.includes('Request failed with status code')) {
      const code = message.substr(message.length - 3)
      if (code === '401' || code === '403') {
        message=null
        if (code === '401') {
          msgBoxNoLogin()
        } else if (code === '403') {
          msgBoxNoRole()
        }
      } else {
        message = 'API' + message.substr(message.length - 3) + 'has problem'
      }
    }
    if (message != null) {
      msgError(message)
    }
    return Promise.reject(error)
  }
)
export default service



